/**
 * @name ScrollToViewDirective
 * @ngkey uiBbScrollToView
 * @type {object}
 * @description
 * Scroll to view component.
 *
 * @example
 * <ui-bb-scroll-to-view="'Asset class name'">
 * </ui-bb-scroll-to-view>
 */

export default function ScrollToViewDirective() {
  return {
    restrict: 'A',
    link: (scope, element, attr) => {
      scope.$watch(() => attr.uiBbScrollToView, (newValue, oldValue) => {
        if (newValue === oldValue || !newValue) return;
        setTimeout(() => {
          const elemScrollTo = element[0].querySelector(`[data-scroll-to="${newValue}"]`);
          if (elemScrollTo) {
            elemScrollTo.scrollIntoView(true);
            if (attr.onFinishScroll) {
              scope.$eval(attr.onFinishScroll);
            }
          }
        }, 0);
      });
    },
    controller() {
    },
    controllerAs: '$scrollToViewCtrl',
  };
}



