/**
 * @module ui-bb-scroll-to-view-module
 *
 * @description
 * Module containing directive used for scroll to view.
 *
 * @example
 * // In an extension:
 * // file: scripts/index.js
 * import uiBbScrollToViewKey from 'ui-bb-scroll-to-view-ng';
 *
 * export const dependencyKeys = [
 *   uiBbScrollToViewKey,
 * ];
 *
 */
import angular from 'vendor-bb-angular';
import ScrollToViewDirective from './directive';

/**
 * @name default
 * @type {string}
 * @description Angular module name
 */
export default angular
  .module('ui-bb-scroll-to-view-ng', [])
  .directive('uiBbScrollToView', ScrollToViewDirective)
  .name;



